<template>
    <div class="size-picker">
      <div class="subtitle">Select Size:</div>
      <div class="button-wrap">
      <!-- TODO: Decide between these options -->
      <!-- OPTION A -->
        <div class="size-option plain button" v-for="size in sizes" :class="{ selected : sizeSelected == size.Value }" @click="selectSize(size.Value)">{{size.DisplayName}}</div>
      <!-- END OPTION A -->
      
      <!-- OPTION B -->
      <!-- <select name="size-select" class="size-select" v-model="selectedSize" @change="selectSize(selectedSize)">
        <option v-for="size in sizes" :value="`${size.Value}`">{{size.DisplayName}}</option>
      </select> -->
      <!-- END OPTION B -->
      </div>
    </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    props: [
      'sizes'
    ],
    data: function() {
      return {
        // TODO: Only needed for option B
        selectedSize: this.$route.params.size
      }
    },
    computed: {
      ...mapGetters({
        sizeSelected: 'getSizeSelectedFromStore'
        })
    },
    methods: {
      ...mapActions({
          selectSize: 'selectSize',
          findPdpProduct: 'findPdpProduct'
      })
    },
    updated() {
      this.findPdpProduct()
    }
  }
</script>



