// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill';
import Vue from 'vue';
import App from './App';
import store from './store';
import router from './router';
import { sync } from 'vuex-router-sync';
import firebase from 'firebase';
import AlgoliaComponents from 'vue-instantsearch';
import VModal from 'vue-js-modal';
import VueAgile from 'vue-agile';
import PrismicDOM from 'prismic-dom';
import Prismic from 'prismic-javascript';
import cloudinary from 'cloudinary';

Vue.use(VueAgile);
Vue.use(AlgoliaComponents);
Vue.use(VModal);
sync(store, router);

Vue.config.productionTip = false;

const linkResolver = function(doc) {
  // Pretty URLs for known types
  if (doc.type === 'blog') return '/blog/' + doc.uid;
  if (doc.type === 'artist') return '/artists/' + doc.uid;
  if (doc.type === 'education') return '/education';
  // Fallback for other types, in case new custom types get created
  return '/doc/' + doc.id;
};

cloudinary.config({
  cloud_name: 'rfathead'
});

let app;
let config = {
  apiKey: 'AIzaSyC18-Jisbl4qldOnksO7xTsWCCC8bUjevo',
  authDomain: 'evolution-883a5.firebaseapp.com',
  databaseURL: 'https://evolution-883a5.firebaseio.com',
  projectId: 'evolution-883a5',
  storageBucket: 'evolution-883a5.appspot.com',
  messagingSenderId: '742293904721'
};

firebase.initializeApp(config);
firebase.auth().onAuthStateChanged(function(user) {
  if (!app) {
    /* eslint-disable no-new */
    app = new Vue({
      el: '#app',
      store,
      router,
      template: '<App/>',
      components: { App }
    });
  }
});



