// START NAV BAR
header {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  .header-cta {
    height: $header-cta-height;
    line-height: $header-cta-height;
    text-align: center;
    background-color: $light-background-color;
  }
  #nav-bar {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    height: $nav-bar-height;
    line-height: $nav-bar-height;
    font-size: 13px;
    width: 100%;
    padding: 0 20px;
    color: $header-text-color;
    background: linear-gradient(
      270deg,
      $medium-background-color,
      $dark-background-color,
      $light-background-color
    );
    background-size: 600% 600%;

    -webkit-animation: LivingGradientAnimation 60s ease infinite;
    -moz-animation: LivingGradientAnimation 60s ease infinite;
    animation: LivingGradientAnimation 60s ease infinite;
    .nav-bar-group {
      display: flex;
      width: 200px;
      a {
        color: $background-color;
        text-decoration: none;
        padding: 0 5px;
        &:hover {
          color: darken($background-color, 10%);
        }
      }
    }
    #nav-bar-right {
      display: flex;
      justify-content: flex-end;
    }
    img {
      height: $nav-bar-height;
    }
    .hamburger {
      width: $bar-width;
      height: $bar-height + $bar-spacing * 2;
      cursor: pointer;
      margin: $hamburger-margin-top 20px 0 0;
      @include xs {
        width: $bar-width-mobile;
        height: $bar-height-mobile + $bar-spacing-mobile * 2;
        margin-top: $hamburger-margin-top-mobile;
      }
      .bar,
      .bar:after,
      .bar:before {
        width: $bar-width;
        height: $bar-height;
        @include xs {
          width: $bar-width-mobile;
          height: $bar-height-mobile;
        }
      }
      .bar {
        position: relative;
        transform: translateY($bar-spacing);
        transition: all 0ms 300ms;
        @include xs {
          transform: translateY($bar-spacing-mobile);
        }

        &.animate {
          background: rgba(255, 255, 255, 0);
        }
      }

      .bar:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: $bar-spacing;
        background: $header-text-color;
        transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
          transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
        @include xs {
          bottom: $bar-spacing-mobile;
        }
      }

      .bar:after {
        content: '';
        position: absolute;
        left: 0;
        top: $bar-spacing;
        background: $header-text-color;
        transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
          transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
        @include xs {
          top: $bar-spacing-mobile;
        }
      }

      .bar.animate:after {
        top: 0;
        transform: rotate(45deg);
        transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1),
          transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
      }

      .bar.animate:before {
        bottom: 0;
        transform: rotate(-45deg);
        transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1),
          transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
      }
    }
  }
  .fullsize-menu {
    width: 100%;
    height: $nav-menu-height;
    position: absolute;
    background-color: $menu-background-color;
    display: flex;
    align-content: center;
    justify-content: center;
    transition: all 0.5s ease;
    box-sizing: border-box;
    font-family: $header-font;
    text-transform: uppercase;
    text-align: center;
    ul {
      list-style-type: none;
      padding: 20vh 0;
      display: flex;
      align-content: space-between;
      flex-direction: column;
      @include xs {
        padding: 8vh 0;
      }
      li {
        font-size: 2em;
        flex-grow: 1;
        @include xs {
          font-size: 1.75em;
        }
        a {
          text-decoration: none;
          color: $medium-background-color;
          &:hover {
            color: lighten($medium-background-color, 20%);
            -webkit-transition: color 0.5s;
            transition: color 0.5s;
          }
        }
      }
    }
  }
}

//Transitions for showing menu
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@-webkit-keyframes LivingGradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@-moz-keyframes LivingGradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes LivingGradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

#nav-bar-branding {
  display: flex;
  flex-direction: row;
  max-width: 600px;
  svg {
    height: $nav-bar-height;
    width: auto;
  }
  .evolution-svg-logo {
    fill: $background-color;
  }
  .brand-line {
    font-size: 24px;
    padding: 0 10px;
  }
}

.router-link-active {
  color: $background-color;
  text-decoration: none;
}

#wordmark-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: $nav-bar-height;
  .altero {
    padding: 0;
    margin: 0;
    font-size: $word-mark-height;
    line-height: $word-mark-height;
    height: $word-mark-height;
  }
}

// END NAV BAR

// START SLIDER HERO
.agile {
  &__dots {
    bottom: 0;
    display: block;
    left: 10px;
    position: absolute;
    width: 30px;
  }

  &__dot {
    margin-bottom: 10px;

    button {
      background-color: transparent;
      border: 1px solid #fff;
      margin-top: 10px;

      &:hover {
        background-color: #fff;
      }
    }

    &--current {
      button {
        background-color: #fff;
      }
    }
  }
}

.slide {
  // looks weird with the logo
  /*  border-top: 5px solid $background-color;
      border-bottom: 5px solid $background-color;*/
  display: flex;
  justify-content: center;
  align-items: center;
  background: {
    position: center;
    size: cover;
  }
  .slide-copy {
    background-color: $dark-background-color;
    color: white;
    padding: 10px 20px;
    text-align: center;
    border: 2px solid $background-color;
  }
  height: 400px;

  &--1 {
    background-image: url(./assets/hero1.png);
  }
  &--2 {
    background-image: url(./assets/hero2.png);
  }
  &--3 {
    background-image: url(./assets/hero3.png);
  }
  &--4 {
    background-image: url(./assets/hero4.png);
  }
}

// END SLIDER HERO

// START HOME PAGE DESTINATIONS
#home-page-destinations {
  width: 100%;
  display: flex;
  justify-content: space-around;
  box-sizing: border-box;
  padding: $standard-padding;
  .wallpaper-rolls {
    background-image: url('./assets/Wallpaper-Rolls-LR.jpg');
    background-size: cover;
  }
  .eames {
    background-image: url('./assets/eames.jpg');
    background-size: cover;
    background-position: center;
  }

  .home-page-destination {
    position: relative;
    width: 30%;
    height: 250px;
    // MAKE THESE RESPONSIVE BASED ON DEVICE HEIGHT
    border: 2px solid $dark-background-color;
    display: flex;
    justify-content: center;
    align-items: center;
    @include under-s {
      width: 50%;
      height: 200px;
    }
    .home-page-destination-copy {
      text-align: center;
      position: relative;
      z-index: 1;
      padding: 5px 10px;
      background-color: $medium-background-color;
      color: $background-color;
      border: 2px solid $background-color;
    }

    .vid {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 0;
      background-color: $background-color;
      overflow: hidden;
    }
    video {
      object-fit: cover;
    }
  }
}

// END HOME PAGE DESTINATIONS
// START CASOUSEL
.hedi {
  background-image: url('./assets/Hedi-Slimane.jpg');
  background-size: cover;
  background-position: center;
}

.carousel-3d-container {
  background-color: $light-background-color;
  margin: 0 auto !important;
  padding: 20px 0;
}

// END CAROUSEL
// START DESIGNER RIBBON
.ribbon-wrap {
  width: 200px;
  height: 200px;
  overflow: hidden;
  position: absolute;
  &.top-right {
    top: -3px;
    right: -3px;
    .ribbon {
      transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      -o-transform: rotate(45deg);
    }
  }
  &:top-left {
    top: -3px;
    left: -3px;
    .ribbon {
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
    }
  }
  .ribbon {
    text-align: center;
    position: relative;
    padding: 15px 0;
    top: 35px;
    left: -60px;
    width: 200%;
    background-color: $designer-series-ribbon-color;
    color: #fff;
    font-size: 1em;
    font-family: $header-font;
  }
}

// END DESIGNER RIBBON
// START MODALS
.vue--modal {
}

.add-to-cart.modal {
  .modal-wrap {
    text-align: center;
  }
  .modal-info-wrap {
    max-width: 700px;
    text-align: left;
    display: inline-block;
  }
  img {
    max-width: 200px;
  }
  .modal-inner {
    width: 100%;
  }
}

.calculator.modal {
  .modal-half {
    img {
      max-width: 100%;
    }
  }
}

.add-to-cart.modal {
  .button-wrap {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 30px 0;
  }
}

.top-bar {
  position: relative;
  padding: 20px 20px 5px 20px;
  .title {
    display: inline-block;
  }
  .modal-close {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 3em;
    line-height: 20px;
    cursor: pointer;
  }
}

.modal-wrap {
  display: flex;
  height: 100%;
  font-family: $base-font;
  @include under-s {
    display: inline-block;
    height: auto;
    width: 100%;
  }
  .modal-half {
    display: inline-block;
    position: relative;
    vertical-align: top;
    height: 100%;
    width: 50%;
    @include under-s {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  .modal-inner {
    padding: 20px;
    .modal-input-group {
      margin: 20px 0;
    }
    .modal-title {
      font-size: 1.25em;
    }
    .modal-number-input {
      width: 75px;
      padding: 5px;
      margin: 5px 0 10px 0;
    }
    .add-wall {
      text-decoration: underline;
      color: $highlight-color;
      cursor: pointer;
    }
    .total,
    .error {
      font-size: 1.25em;
      margin: 15px 0;
    }
    .total {
      color: $success;
    }
    .error {
      color: $warning;
    }
    .red-border {
      border: 2px solid $warning;
    }
  }
  .modal-image {
    @include under-s {
      display: none;
      // width: 100%;
      // position: absolute;
      // height: 100%;
      // top: 0;
      // left: 0;
      // opacity: 0.05;
      // background-repeat: repeat;
      // z-index: 1;
    }
  }
}

// END MODALS
// START WALLPAPER PAGE
.ais-results {
  display: grid;
  grid-gap: 120px 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 210px;
  margin-bottom: 120px;
  @include xs {
    grid-auto-rows: 240px;
  }
}

.input-group {
  display: flex;
  flex-direction: row;
  width: 100%;
  font-family: $base-font;
  color: $content-text-color;
  background-color: #fff;
  border: 2px solid #f0f3f5;
  .search-input {
    padding-left: 15px;
    width: 80%;
    height: 50px;
    border-left: none;
    border-top: none;
    border-bottom: none;
  }
  .submit-search-button {
    font-family: $base-font;
  }
  button {
    cursor: pointer;
    width: 20%;
    background-color: #fff;
    border: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-content: center;
    &:hover {
      background-color: $content-accent-color;
    }
  }
}

.ais-price-range {
  input.ais-price-range__input {
    font-family: $base-font;
    padding-left: 2%;
  }
}

.ais-refinement-list {
  .ais-refinement-list__item {
    .ais-refinement-list__label {
      .ais-refinement-list__checkbox {
        background-color: #fff;
        border-radius: 0;
      }
    }
  }
}

.patterns {
  display: grid;
  grid-gap: 45px 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 200px;
}

.ais-index {
  width: 100%;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  position: relative;
}

.filter-search {
  .filter-wrap {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    position: relative;
  }

  .filters {
    width: $filter-width;
    min-width: 300px;
    color: $content-text-color;
    box-sizing: border-box;
    padding: 0 1% 0 0;
    @include under-m {
      display: none;
    }
  }
  .filters-mobile {
    background-color: $background-color;
    display: none;
    position: fixed;
    width: 70%;
    height: 100%;
    box-sizing: border-box;
    padding: 2% 5%;
    z-index: 9999;
    overflow: scroll;
    right: 0;
    top: 0;
    @include under-m {
      display: block;
    }
  }
  .close {
    cursor: pointer;
  }
  .overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 99;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .results {
    width: $results-width;
    color: $content-text-color;
    padding: 0 0 0 1%;
    box-sizing: border-box;
    @include under-s {
      width: 100%;
    }
    .button-wrap {
      width: 100%;
      display: inline-block;
      @include over-m {
        display: none;
      }
      #mobile-filters-button {
        float: right;
        margin: 5px 0;
        @include over-m {
          display: none;
        }
      }
    }
  }
}

.ais-sort-by-selector {
  font-family: $base-font;
  width: 100%;
  padding: 5px;
  margin: 10px 0;
  border: 1px solid $content-accent-color;
}

.ais-refinement-list__count {
  color: $warning;
  text-decoration: underline;
  margin-left: 2.5px;
}

.ais-refinement-list__checkbox {
  border: 1px solid $warning;
}

.activeFilter {
  outline: $swatch-outline-width solid $highlight-color;
  outline-offset: $swatch-outline-offset;
}

.activePatternFilter:before {
  content: 'X ';
  color: $highlight-color;
}

.activeFilterText {
  font-weight: 800;
}

.color-filters {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.pattern-filter-option {
  cursor: pointer;
}

$swatch-size: 25px;
.color-swatch {
  box-sizing: border-box;
  height: $swatch-size;
  width: $swatch-size;
  margin: $swatch-margin;
  border: 1px solid $content-accent-color;
}

.filter-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.pagination-wrap {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  margin-bottom: 30px;
  padding: 0;
  .results-per-page-text {
    align-self: center;
  }
  .ais-results-per-page-selector {
    display: inline-block;
    margin: 0 5px 0 20px;
  }
  ul.pagination {
    padding: 0;
    margin: 0;
    display: inline-block;
    .ais-pagination__item {
      margin: 0 5px;
      background-color: #fff;
      padding: 5px 10px;
      border: 1px solid $input-border-color;
      .ais-pagination__link {
        text-decoration: none;
        color: $content-text-color;
        font-family: $base-font;
        font-size: 1.25em;
      }
    }
    li {
      display: inline-block;
      padding: 0 5px;
    }
  }
}
.result__info {
  text-align: center;
  background-color: white;
  padding: 10px 0 0 0;
  margin-top: -4px;
  border: $base-border;
  border-top: 0px;
  .result__price {
    margin: 5px 0;
    font-size: 1em;
  }
}
.wallpaper-pattern {
  word-wrap: break-word;
  background-color: white;
  color: $content-text-color;
  display: flex;
  justify-content: center;
  position: relative;
  .designer-series-ribbon {
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: absolute;
    top: 0px;
    left: 0px;
    .ribbon {
      color: #333;
      text-align: center;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      position: relative;
      padding: 7px 0;
      top: 15px;
      left: -30px;
      width: 120px;
      background-color: $designer-series-ribbon-color;
      color: #fff;
    }
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: $base-border;
    border-bottom: none;
    box-sizing: border-box;
  }
  a {
    text-decoration: none;
    color: $highlight-color;
  }
  h1,
  h2,
  h3,
  h4,
  h5 {
    padding: 0;
    margin: 0;
    text-align: center;
  }
  .color-options {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    .color-option {
      height: 30px;
      flex-grow: 1;
      position: relative;
      cursor: pointer;
      &:before {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.25);
        opacity: 0;
        content: '';
      }
      &:hover {
        &:before {
          opacity: 1;
        }
      }
      &.selected {
        display: flex;
        justify-content: center;
        align-items: center;
        &:before {
          opacity: 1;
          border-radius: 100px;
          background-color: #fff;
          width: 10px;
          height: 10px;
          margin-right: -5px;
          margin-left: -5px;
          position: relative;
        }
      }
    }
  }
}

// END WALLPAPER PAGE
// START PDP
.pdp.page {
  .container {
    padding: 20px;
    background-color: $background-color;
    .content-block {
      background-color: $background-color;
    }
  }
}
.pdp-wrap {
  display: inline-block;
  width: 100%;
  @include under-m {
    width: 100%;
  }
  .pdp-inner {
    text-align: center;
    display: flex;
    align-content: center;
    flex-direction: row;
    @include under-s {
      flex-direction: column;
    }
    .image-wrap {
      width: 55%;
      @include under-s {
        width: 100%;
      }
    }
    .product-detail-wrap {
      margin-left: 1em;
      width: 45%;
      text-align: left;
      @include under-s {
        margin-left: 0;
        width: 100%;
        align-self: center;
        margin-top: 15px;
      }
      .add-to-cart {
        margin: 0 0 30px 0;
        .button {
          width: 100%;
          text-align: center;
          padding-left: 0;
          padding-right: 0;
          max-width: 300px;
          @include under-s {
            max-width: 1000px;
          }
        }
      }
      .price {
        font-size: 1.75em;
        margin: 30px 0;
      }
      .material-picker {
        .button-wrap {
          display: flex;
          justify-content: space-between;
          width: 100%;
          max-width: 300px;
          //TODO: for option B
          // max-width: 459px;
          // margin: 0;
          margin: 0 0 20px 0;
          @include under-s {
            max-width: 1000px;
          }
          .material-option {
            width: 100px;
            padding: 10px 20px;
            text-align: center;
            cursor: pointer;
            line-height: 1.25em;
            &.selected {
              border-color: $highlight-color;
              background-color: $highlight-color;
              color: #fff;
            }
            @include under-s {
              width: 48%;
              padding: 10px 0;
            }
          }
          .material-select {
            width: 400px;
            padding: 10px;
            margin: 20px 0;
            font-size: 1em;
            text-transform: capitalize;
          }
        }
      }
      .size-picker {
        .button-wrap {
          display: flex;
          justify-content: space-between;
          align-content: space-between;
          width: 100%;
          max-width: 300px;
          flex-wrap: wrap;
          // TODO: for option B
          // margin: 0;
          margin: 0 0 20px 0;
          @include under-s {
            max-width: 1000px;
          }
          .size-option {
            width: 100px;
            padding: 10px 20px;
            margin: 5px 0;
            text-align: center;
            cursor: pointer;
            line-height: 1.25em;
            &.selected {
              border-color: $highlight-color;
              background-color: $highlight-color;
              color: #fff;
            }
            @include under-s {
              width: 48%;
              padding: 10px 0;
            }
          }
          .size-select {
            width: 400px;
            padding: 10px;
            margin: 20px 0;
            font-size: 1em;
          }
        }
      }
      .color-picker {
        margin: 20px 0;
        max-width: 300px;
        @include under-s {
          max-width: 1000px;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
        }
        .color-option {
          width: 40px;
          height: 40px;
          cursor: pointer;
          border: none;
          padding: 0;
          border-radius: 0;
          transition: border 0s;
          -webkit-transition: border 0s;
          box-sizing: border-box;
          overflow: hidden;
          margin-right: 15px;
          &:last-child {
            margin-right: 0;
          }
          &.selected {
            // TODO: Do we want this outline or the corner option?
            // outline: 2px solid $selected-pdp-color;
            &:before {
              content: '';
              display: inline-block;
              color: #333;
              text-align: center;
              -webkit-transform: rotate(-45deg);
              -moz-transform: rotate(-45deg);
              -ms-transform: rotate(-45deg);
              -o-transform: rotate(-45deg);
              position: relative;
              padding: 7px 0;
              top: -40px;
              left: -40px;
              width: 50px;
              height: 50px;
              background-color: $cta-color;
            }
          }
          @include under-s {
            padding: 0 10px;
            height: 50px;
            width: 48%;
            margin-top: 10px;
            margin-right: 0;
          }
        }
      }
    }
    .pdp-image {
      width: 80%;
    }
  }
}

#room-view {
  //TODO : THIS NEEDS TO BE DYNAMICALLY UPDATED based on ROOM SIZE
  background-size: 100px 100px;
  //TODO : if nonstandard size then this needs to updated
  // TODO : make sure updated data accounts for pattern size
  overflow: hidden;
  transition: background-image 0.2s ease-in-out;
  height: 500px;
  @include under-s {
    height: 45vh;
  }
}
#cloud-layers {
  background-image: url('./assets/room-preview/ajax-loader.gif');
  background-repeat: no-repeat;
  background-position: center center;
  min-height: 200px;
  width: 100%;
  display: inline-block;
}
#room-picker {
  .button {
    &.selected {
      border-color: $highlight-color;
      background-color: $highlight-color;
      color: #fff;
    }
  }
}

.room-view-image {
  max-width: 100%;
  margin-top: 50px;
}

.social-media-icon {
  color: $cta-color;
  font-size: 1.5em;
  margin: 10px 2.5px;
  &:hover {
    color: darken($cta-color, 10%);
  }
}
#room-preview {
  position: relative;
  width: 100%;
  min-height: 200px;
  .room-options {
    position: absolute;
    bottom: 20px;
    right: 20px;
    z-index: 10000;
    background-color: #383838;
    color: white;
    text-align: center;
    border-radius: 1%;
    padding: 5px;
    font-family: Helvetica, sans-serif;
  }
  #pattern-preview {
    width: 100%;
    height: 1500px;
  }
  img {
    width: 100%;
  }
}
// END PDP
// START QUANTITY SELECTOR
.quantity-selector {
  display: flex;
  margin: 20px 0;
  .minus-one,
  .plus-one {
    font-size: 1.25em;
    width: 40px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    padding: 0 !important;
    border-radius: 0;
  }
  .quantity-selected {
    font-size: 1.25em;
    width: 40px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    border: 1px solid $content-text-color;
    box-sizing: border-box;
  }
}
//END QUANTITY SELECTOR
// START CART
.cart.page {
  .container {
    .cart-flex {
      display: flex;
      justify-content: center;
      flex-direction: row;
      box-sizing: border-box;
      position: relative;
      @include under-s {
        flex-direction: column-reverse;
      }
    }
  }
}

.cart-list {
  width: 70%;
  box-sizing: border-box;
  display: inline-block;
  @include under-m {
    width: 60%;
  }
  @include under-s {
    width: 100%;
  }
  .cart-list-inner {
    width: 90%;
    margin: auto;
  }
}

.cart-total {
  margin-top: 100px;
  width: 30%;
  box-sizing: border-box;
  display: inline-block;
  display: flex;
  flex-direction: column;
  @include under-m {
    width: 40%;
  }
  @include under-s {
    width: 100%;
    margin-left: 0;
  }
  .total-inner {
    padding: 20px;
    background-color: $background-color;
    .button {
      display: block;
    }
  }
  .cart-line {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
  }
}

.cart-item {
  display: grid;
  grid-template-columns: 150px 1fr 100px;
  grid-template-rows: auto 1fr auto;
  margin-bottom: 50px;
  grid-column-gap: 20px;
  @include under-m {
    grid-template-rows: auto 1fr;
    grid-row-gap: 5px;
  }
  @include xs {
    grid-template-rows: auto auto auto auto auto;
    grid-template-columns: 1fr;
    grid-row-gap: 5px;
  }
  .cart-image-wrap {
    grid-area: 1 / 1 / -1 / 2;
    @include xs {
      grid-area: 1 / 1 / 2 / -1;
    }
    .cart-image {
      width: 150px;
      height: 150px;
      display: inline-block;
      @include xs {
        width: 100%;
        height: auto;
      }
    }
  }

  .pattern-name {
    grid-area: 1 / 2 / 2 / 3;
    @include xs {
      grid-area: 2 / 1 / 3 / -1;
    }
  }

  .button-wrap {
    grid-area: 3 / 2 / -1 / 3;
    @include under-m {
      grid-area: 2 / 2 / 3 / 3;
      align-self: center;
    }
    @include xs {
      grid-area: 5 / 1 / 6 / -1;
    }
    .tiny.red.button {
      margin: 5px 0;
    }
  }
  .cart-item-price {
    grid-area: 1 / 3 / 2 / 4;
    font-size: 1.5em;
    @include under-m {
      grid-area: 2 / 3 / 3 / 4;
      align-self: center;
    }
    @include xs {
      grid-area: 4 / 1 / 5 / -1;
      font-size: 1.25em;
    }
  }
}

// END CART
// START ARTISTS
.artists.page {
  .artists-wrap {
    display: grid;
    grid-template-columns: 1fr minmax(300px, 25%);
    margin: 5px 0;
    grid-column-gap: 20px;
    @include under-m {
      grid-template-columns: 100%;
    }
    .artist-list {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
      grid-auto-rows: auto;
      grid-column-gap: 30px;
      .artist-content-block {
        background-color: $content-background-color;
        display: inline-block;
        height: auto;
        img {
          max-width: 100%;
        }
      }
    }
  }
}

// END ARTISTS
// START BLOG
.blog.page {
  .content-wrap {
    display: grid;
    grid-template-columns: 1fr minmax(300px, 25%);
    margin: 5px 0;
    grid-column-gap: 20px;
    @include under-m {
      grid-template-columns: 100%;
    }
  }
}

// END BLOG
// START EDUCATION
.education.page {
  .education-wrap {
    display: grid;
    grid-template-columns: 1fr 40%;
    grid-template-rows: auto 1fr;
    grid-column-gap: 20px;
    @include under-m {
      grid-template-columns: 1fr 25%;
    }
    @include under-s {
      grid-template-columns: 1fr;
      grid-template-rows: auto;
      grid-row-gap: 20px;
    }

    .education-title {
      grid-row: 1 / 2;
      grid-column: 1 / -1;
    }
    .education-video-text {
      grid-row: 2 / -1;
      grid-column: 2 / -1;
      @include under-s {
        grid-column: 1 / -1;
        grid-row: auto;
      }
      p {
        margin-top: 0;
      }
    }
    .education-video-wrap {
      position: relative;
      padding-bottom: 56.25%; /* 16:9 */
      padding-top: 25px;
      height: 0;
      grid-row: 2 / -1;
      grid-column: 1 / 2;
      iframe {
        border: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  li {
    list-style-position: inside;
  }
}

//END EDUCATION
// START LANDING PAGE
.landing-page-wrap {
  .banner {
    width: 100%;
    height: 150px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    .header {
      position: absolute;
      align-self: center;
      margin: 0;
      color: $content-text-color;
    }
    img {
      width: 100%;
      position: relative;
    }
  }
}

// END LANDING PAGE
// START MARKETING PANEL
.marketing-panel {
  text-align: center;
  .text-wrap {
    text-align: left;
  }
  .content-block {
    padding: 10px 20px;
    background-color: $background-color;
  }
  img {
    max-width: 75%;
    margin: 30px 0;
  }
}

// END MARKETING PANEL

// START SMART FILTER
#smart-filter {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  #step-header {
    margin: 0;
    padding: 0;
    h2 {
      margin: 10px 0 0;
      padding: 0;
    }
  }
  .step-body {
    min-height: 644px;
    background-color: #fff;
    padding: 20px;
    margin: 20px;
    border: 1px solid $dark-background-color;
    border-radius: 2px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    .step-option {
      cursor: pointer;
      border: 1px solid $dark-background-color;
      height: 300px;
      width: 400px;
      margin: 10px;
      display: flex;
      flex-direction: column;
      background-color: $background-color;
      img {
        //position: absolute;
        height: 210px;
        width: 400px;
        left: 0;
        top: 0;
      }
      h2 {
        padding: 10px 0 0;
        margin: 0;
      }
      p {
        padding: 0;
        margin: 10px 0 0;
      }
      &:hover {
      }
    }

    $color-pallete-width: 200px;
    .color-palette {
      cursor: pointer;
      width: $color-pallete-width;
      height: 150px;
      position: relative;
      border: 1px solid $dark-background-color;
      margin: 10px;
      .color-container {
        width: $color-pallete-width;
        height: 100px;
        position: absolute;
        display: flex;
        flex-direction: row;
        padding: 0;
        margin: 0;
        top: 0;
        left: 0;
        .single-color {
          width: 25%;
        }
      }
      .palette-description {
        width: $color-pallete-width;
        position: absolute;
        left: 0;
        bottom: 0;
        line-height: 50px;
      }
    }
  }

  .color-choice-circle {
    cursor: pointer;
    width: 150px;
    height: 150px;
    border-radius: 100%;
    position: relative;
    margin: 30px;
    border: 1px solid $dark-background-color;
    .description {
      position: absolute;
      // center : margin-top: 62.5px;
      margin-top: 155px;
      height: 25px;
      background-color: #fff;
      line-height: 25px;
      width: 175px;
      left: -12.5px;
      border: 1px solid $dark-background-color;
    }
  }
  .peach {
    background-image: linear-gradient(
      45deg,
      #ff9a9e 0%,
      #fad0c4 99%,
      #fad0c4 100%
    );
  }
  .purple {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
  }
  .yellow {
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  }
  .light-blue {
    background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  }
  .light-grey {
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
  }
  .pink {
    background-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%);
  }
  .light-grey {
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  }
  .bright-blue {
    background-image: radial-gradient(
      circle 248px at center,
      #16d9e3 0%,
      #30c7ec 47%,
      #46aef7 100%
    );
  }
  .plum {
    background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  }
  .beige {
    background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
  }
  .dark-grey {
    background: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0.15) 0%,
        rgba(0, 0, 0, 0.15) 100%
      ),
      radial-gradient(
          at top center,
          rgba(255, 255, 255, 0.4) 0%,
          rgba(0, 0, 0, 0.4) 120%
        )
        #989898;
    background-blend-mode: multiply, multiply;
  }
  .orange {
    background-image: linear-gradient(
      to right,
      #f78ca0 0%,
      #f9748f 19%,
      #fd868c 60%,
      #fe9a8b 100%
    );
  }
  #step-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .step-btn {
      cursor: pointer;
      padding: 20px;
      margin: 5px 20px;
      border-radius: 2px;
      background-color: $dark-background-color;
      color: #fff;
      &:hover {
        background-color: darken($dark-background-color, 10%);
      }
    }
  }
}

// END SMART FILTER

// START FOOTER
footer {
  width: 100%;
  z-index: 99;
  color: #fff;
  background: linear-gradient(
    270deg,
    $medium-background-color,
    $dark-background-color,
    $light-background-color
  );
  background-size: 600% 600%;

  -webkit-animation: LivingGradientAnimation 60s ease infinite;
  -moz-animation: LivingGradientAnimation 60s ease infinite;
  animation: LivingGradientAnimation 60s ease infinite;
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  h2 {
    margin: 0;
  }
  h3 {
    margin: 0 0 20px;
  }
  h5 {
    margin: 0;
  }
  @include xs {
  }
  #footer-actions {
    display: flex;
    justify-content: center;
    flex-direction: row;
    h4 {
      padding: 5px 30px;
      text-decoration: underline;
    }
    a {
      color: #deddf3;
    }
    @include under-s {
      flex-direction: column;
      h4 {
        padding: 0;
      }
    }
  }
  .social-media-footer-icons {
    font-size: 3em;
    transition: all 0.5s ease;
    cursor: pointer;
    a {
      color: #fff;
      text-decoration: none;
    }
    :hover {
      color: #cee8f4;
      transition: all 0.5s ease;
    }
    @include under-s {
      font-size: 2em;
    }
  }
  #footer-svg-wrapper {
    svg {
      fill: $background-color;
      height: 70px;
    }
  }
}

// END FOOTER;
</style>



