<template>
    <div v-if="pattern" id="room-view" v-bind:style="{ backgroundImage : 'url('+pattern.Images.Full+')' }">
        <!-- {{pattern}} -->
        <!-- <img :src="`${pdpRoom.image}`" :alt="`${pdpRoom.name}`" class="room-view-image"> -->
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'

    export default {
        props: ['pattern'],
        computed: {
            ...mapGetters({
                pdpRoom: 'getPdpRoomFromStore',
            })
        },
    }
</script>



