<template>
  <div class="education page">
    <landing-content :data="landingContent"></landing-content>
    <div class="container">
      <div class="education-wrap">
        <div class="education-title">
          <h1>{{info.data.education_title[0].text}}</h1>
        </div>
        <div class="education-video-wrap">
          <iframe :src="`https://youtube.com/embed/${info.data.installation_video.embed_url.split('=')[1]}`"></iframe>
        </div>
        <div class="education-video-text">
          <p v-for="line in info.data.video_text">{{line.text}}</p>
          <div class="education-details">
            <serializer :content='info.data.installation_directions'></serializer>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import LandingContent from './marketing/LandingContent';
import Serializer from './serializer/Serializer';

export default {
  components: {
    LandingContent,
    Serializer
  },
  computed: {
    ...mapGetters({
      info: 'getEducationFromStore',
      landingContent: 'getLandingContentFromStore'
    })
  },
  methods: {
    ...mapActions({
      getEducation: 'getEducation',
      getLandingContent: 'getLandingContent'
    })
  },
  mounted() {
    this.getEducation();
    this.getLandingContent(this.$route.name);
    window.scrollTo(0, 0);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



