<template>
  <div class="artist page">
    <div class="container">
      <div class="content-block">
        <router-link to="/artists" class="button">Back</router-link>
        <div class="title">{{pageArtist.data.artist_name[0].text}}</div>
        <img :src="`${pageArtist.data.artist_picture.url}`" :alt="`${pageArtist.data.artist_name[0].text}`"><br>
        <serializer :content='pageArtist.data.artist_bio'></serializer>
      </div>
    </div>
    <hr>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Serializer from './../serializer/Serializer';

export default {
  components: { Serializer },
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    };
  },
  computed: {
    ...mapGetters({
      pageArtist: 'getPageArtistFromStore'
    })
  },
  methods: {
    ...mapActions({
      getPageArtist: 'getPageArtist'
    })
  },
  mounted() {
    this.getPageArtist(this.$route.params.artist);
    window.scrollTo(0, 0);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



