<template>
  <div class="artists page">
    <landing-content :data="landingContent"></landing-content>
    <div class="container">
      <div class="artists-wrap">
        <div class="artist-list">
          <div v-for="artist in artists" class="artist-content-block">
            <router-link :to="`/artists/${artist.uid}`"><img :src="`${artist.data.featured_work.url}`"></router-link>
            <router-link :to="`/artists/${artist.uid}`">
              <div>{{artist.data.artist_name[0].text}}</div>
              <span class="subtext">{{artist.data.occupation}}</span>
            </router-link>
            <p>{{artist.artist_bio}}</p>
          </div>
        </div>
        <marketing-panel v-bind:page="'artists'"></marketing-panel>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import MarketingPanel from './marketing/MarketingPanel';
import LandingContent from './marketing/LandingContent';

export default {
  components: {
    MarketingPanel,
    LandingContent
  },
  name: 'Artists',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    };
  },
  computed: {
    ...mapGetters({
      artists: 'getArtistsFromStore',
      landingContent: 'getLandingContentFromStore'
    })
  },
  methods: {
    ...mapActions({
      getArtistsHere: 'getArtists',
      getLandingContent: 'getLandingContent'
    })
  },
  mounted: function() {
    this.getArtistsHere();
    this.getLandingContent(this.$route.name);
    window.scrollTo(0, 0);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

</style>



