<template>
  <div class="cart page">
    <div class="container">
      <div class="cart-flex">
        <div class="content-block cart-list">
          <div class="cart-list-inner">
            <h1>cart</h1>
            <cart-items :cart="cart"></cart-items>
            <!--TODO : cart stuff-->
          </div>
        </div>
        <cart-total :cart="cart"></cart-total>
      </div>
    </div>

  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import CartItems from './cart/CartItems';
import CartTotal from './cart/CartTotal';

export default {
  components: {
    CartItems,
    CartTotal
  },
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    };
  },
  computed: {
    ...mapGetters({
      cart: 'getCartFromStore'
    })
  },
  methods: {
    ...mapActions({
      getCartHere: 'getCart',
      removeFromCart: 'removeFromCart'
    })
  },
  created() {
    window.scrollTo(0, 0);
    this.getCartHere();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



