<template>
  <modal name="add-to-cart" width="700px" height="600px" :adaptive="true" class="add-to-cart modal">
    <div class="modal-wrap">
      <div class="modal-inner">
        <div class="title">{{product.DisplayName}} Added to Cart!</div>
        <div class="modal-info-wrap">
          <img :src="`${product.Images.Thumbnail}`" :alt="`${product.DisplayName}`">
          <p>Material: {{product.Material}}</p>
          <p>Color: {{product.ColorWay.ColorWayDisplayName}}</p>
          <cart-quantity-selector :product="productAdded"></cart-quantity-selector>
        </div>
        <div class="button-wrap">
          <router-link to="/wallpaper" class="button">Continue Shopping</router-link>
          <router-link to="/cart" class="button">Checkout &#x21E2;</router-link>
        </div>

        <h2>Make Installation Easier!</h2>
        <p>-----Upsells here-----</p>
        <!-- TODO: <upsells></upsells> -->
      </div>
    </div>
  </modal>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import CartQuantitySelector from './../cart/CartQuantitySelector';

export default {
  components: {
    CartQuantitySelector
  },
  props: ['product'],
  data() {
    return {};
  },
  methods: {
    hideModal() {
      this.$modal.hide('add-to-cart');
    }
  },
  computed: {
    ...mapGetters({
      productAdded: 'getProductAddedFromStore'
    })
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



