<template>
    <div class="pattern-info">
      <div class="title">{{info.DisplayName}}</div>
      <div class="subtext">By <router-link :to="info.ArtistUrl">{{info.ArtistDisplayName}}</router-link></div>
      <h3>{{info.PrimaryCategory.DisplayName}}</h3>
      <p>{{info.Description}}</p>
    </div>
</template>

<script>
  export default {
/*    components : {
      WallpaperPattern
    },*/
    props: [
      'info'
    ],
  }
</script>



