<template>
  <div id="room-preview">
    <div class="room-options">
      <input type="file" class="upload-banner-image" id="uploadInput" @change="readURL">
      <select class="select-size" v-model="selectedSize">
        <option value="63">6"x6"</option>
        <option value="83">8"x8"</option>
        <option value="125">12"x12"</option>
        <option value="250">24"x24"</option>
      </select>
      <select class="select-room" v-model="selectedRoom">
        <option value="kids">Kids</option>
        <option value="nursery">Nursery</option>
        <option value="industrial">Industrial</option>
        <option value="office">Office</option>
      </select>
      <br>
    </div>
    <div class="room-layers">
      <span id="cloud-layers" v-html='cloud'></span>
      <span style="display: none" v-html='preload1'></span>
      <span style="display: none" v-html='preload2'></span>
      <span style="display: none" v-html='preload3'></span>
      <span style="display: none" v-html='preload4'></span>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import cloudinary from 'cloudinary';

export default {
  data: function() {
    return {
      selectedSize: 250,
      backgroundImage: 'test_patterns:eye_balls',
      selectedRoom: 'kids'
    };
  },
  methods: {
    readURL() {
      let input = document.getElementById('uploadInput');
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = e => {
          cloudinary.v2.uploader.unsigned_upload(
            e.target.result,
            'pv3o8tmu',
            { folder: 'test_patterns/' },
            (error, result) => {
              let original = result.public_id;
              this.backgroundImage = original.replace('/', ':'); // Cloudinary urls require : instead of / for folders
            }
          );
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    cloudinaryImg(background, size) {
      return cloudinary.image(`test_rooms/wall_${this.selectedRoom}`, {
        transformation: [
          {
            overlay: background,
            flags: 'tiled',
            opacity: 85,
            effect: 'multiply',
            width: size
          },
          {
            effect: 'blur:10'
          },
          {
            overlay: `test_rooms:furniture_${this.selectedRoom}`
          },
          {
            overlay: 'test_rooms:noise',
            effect: 'multiply'
          },
          {
            overlay: `test_rooms:shadows_${this.selectedRoom}`,
            effect: 'multiply'
          },
          {
            overlay: `test_rooms:lighting_${this.selectedRoom}`,
            effect: 'multiply'
          }
        ]
      });
    },
    // TODO: Implement cloudinary upload widget?
    openWidget() {
      cloudinary.openUploadWidget(
        { cloud_name: 'rfathead', upload_preset: 'f05u9z4r' },
        (error, result) => {
          console.log(error, result);
        }
      );
    }
  },
  computed: {
    cloud() {
      return this.cloudinaryImg(this.backgroundImage, this.selectedSize);
    },
    preload1() {
      if (this.selectedSize != 250) {
        return this.cloudinaryImg(this.backgroundImage, 250);
      } else {
        return '';
      }
    },
    preload2() {
      if (this.selectedSize != 125) {
        return this.cloudinaryImg(this.backgroundImage, 125);
      } else {
        return '';
      }
    },
    preload3() {
      if (this.selectedSize != 83) {
        return this.cloudinaryImg(this.backgroundImage, 83);
      } else {
        return '';
      }
    },
    preload4() {
      if (this.selectedSize != 63) {
        return this.cloudinaryImg(this.backgroundImage, 63);
      } else {
        return '';
      }
    }
  }
};
</script>
<style <style lang="scss" scoped>

</style>



