<template>
  <div class="wallpaper-pattern">
    <!-- <div class="designer-series-ribbon">
            <div class="ribbon">Designer</div>
        </div> -->
    <div class="result-wrap">
      <router-link :to="{ name: 'PDP', params: { pattern: result.ProductUrl, colorway: selectedColor.ColorWayName } }">

        <img :src="result.Images.DefaultThumbnail" v-if="!selectedColor">
        <img :src="selectedColor.Images.Thumbnail" v-else>
      </router-link>
      <div class="result__info">
        <router-link :to="{ name: 'PDP', params: { pattern: result.ProductUrl, colorway: selectedColor.ColorWayName } }">
          <h2 class="result__name">
            <!-- <ais-highlight :result="result" attribute-name="design.name" /> -->
            {{result.DisplayName}}
          </h2>
          <!-- <div class="result__type">
                    <ais-highlight :result="result" attribute-name="material" />
                    {{result.material}}
                </div> -->
          <!--           <div class="result__rating">
                                <template v-for="n in 5">
                                <span v-if="n <= result.rating" class="result__star" :key="n"></span>
                                <span v-else class="result__star&#45;&#45;empty" :key="n"></span>
                                </template>
                            </div>-->
          <div class="result__price">${{result.Price}}</div>
        </router-link>

        <div class="color-options">
          <div class="color-option" v-for='color in result.Colors' :style="{backgroundColor: color.ColorWayHex}" @click="selectColor(color)" :class="{selected: selectedColor == color}"></div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
/*
import AddFavorite from './../favorites/AddFavorite';
*/

export default {
  props: ['result'],
  components: {
    /*AddFavorite*/
  },
  data() {
    return {
      selectedColor: this.result.Colors[0]
    };
  },
  methods: {
    selectColor(color) {
      this.selectedColor = color;
    }
  }
};
</script>



