import Vue from 'vue';
import Router from 'vue-router';

import Artists from '@/components/Artists';
import ArtistPage from '@/components/artists/ArtistPage';
import Blog from '@/components/Blog';
import BlogPost from '@/components/blog/BlogPost';
import Education from '@/components/Education';
import Wallpaper from '@/components/Wallpaper';
import PDP from '@/components/PDP';
import Cart from '@/components/Cart';
import RoomPreview from '@/components/pdp/RoomPreview';
import Hello from '@/components/Hello';
import Login from '@/components/Login';
import SignUp from '@/components/SignUp';
import Home from '@/components/Home';
import SmartFilter from '@/components/SmartFilter';
import SwipeFilter from '@/components/SwipeFilter';
import Cloudinary from '@/components/pdp/Cloudinary';

// FOR AUTHENTICATION
import firebase from 'firebase';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/wallpaper',
      name: 'wallpaper',
      component: Wallpaper
    },
    {
      path: '/product/:pattern/:colorway?/:material?/:size?',
      //https://github.com/vuejs/vue-router/blob/dev/examples/named-routes/app.js
      name: 'PDP',
      component: PDP
    },
    {
      path: '/room-preview',
      name: 'Room Preview',
      component: RoomPreview
    },
    {
      path: '/cloudinary',
      name: 'Cloudinary',
      component: Cloudinary
    },
    {
      path: '/cart',
      //https://github.com/vuejs/vue-router/blob/dev/examples/named-routes/app.js
      name: 'Cart',
      component: Cart
    },
    {
      path: '/artists',
      name: 'artists',
      component: Artists
    },
    {
      path: '/artists/:artist',
      name: 'artist page',
      component: ArtistPage
    },
    {
      path: '/blog',
      name: 'blog',
      component: Blog
    },
    {
      path: '/blog/:post',
      name: 'Blog Post',
      component: BlogPost
    },
    {
      path: '/education',
      name: 'education',
      component: Education
    },
    /*    {
      path: '*',
      redirect: '/login'
    },*/
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/smart-filter',
      name: 'Smart Filter',
      component: SmartFilter
    },
    {
      path: '/swipe-filter',
      name: 'Swipe Filter',
      component: SwipeFilter
    },
    {
      path: '/sign-up',
      name: 'SignUp',
      component: SignUp
    },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello,
      meta: {
        requiresAuth: true
      }
    }
  ]
});

router.beforeEach((to, from, next) => {
  let currentUser = firebase.auth().currentUser;
  let requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if (requiresAuth && !currentUser) {
    /*  next('login')*/
    next();
  } else if (!requiresAuth && currentUser) {
    /* next('hello')*/
    next();
  } else {
    next();
  }
});

export default router;



