<template>
  <div class="blog page">
    <!-- TODO : Blog Params?? -->
    <landing-content :data="landingContent"></landing-content>
    <div class="container">
      <div class="content-wrap">
        <div class="blog-posts">
          <div class="content-block" v-for="blog in blogs">
            <router-link :to="`/blog/${blog.uid}`">
              <div class="title">{{blog.data.title[0].text}}</div>
            </router-link>
            <div class="subheader">{{blog.data.subheader[0].text}}</div>
            <span class="subtext">By {{blog.data.author[0].text}} on {{blog.data.article_date}}</span>
          </div>
        </div>
        <marketing-panel v-bind:page="'blog'"></marketing-panel>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import MarketingPanel from './marketing/MarketingPanel';
import LandingContent from './marketing/LandingContent';

export default {
  components: {
    MarketingPanel,
    LandingContent
  },
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    };
  },
  computed: {
    ...mapGetters({
      blogs: 'getBlogsFromStore',
      landingContent: 'getLandingContentFromStore'
    })
  },
  methods: {
    ...mapActions({
      getBlogsHere: 'getBlogs',
      getLandingContent: 'getLandingContent'
    })
  },
  mounted() {
    this.getBlogsHere();
    console.log('this.$route.name', this.$route.name);
    this.getLandingContent(this.$route.name);
    window.scrollTo(0, 0);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



