<template>
  <div id="room-preview">
    <div class="room-options">
      <input type='file' class="upload-banner-image" onchange="readURL(this);" />

      <!--
    <h1 id="currentValue">250px</h1>
-->
      <select class="select-size" v-model="selectedSize">
        <option altvalue="31.5" value="60">3"x3"</option>
        <option altvalue="41.5" value="45">4"x4"</option>
        <option altvalue="63" value="30">6"x6"</option>
        <option altvalue="83" value="25">8"x8"</option>
        <option altvalue="125" value="15">12"x12"</option>
        <option altvalue="250" value="7.8">24"x24"</option>
      </select>
      <br>
      <!--
    <input type="range" min="25" max="1000" value="50" class="slider" id="imageRepeatSize"><br>
-->

    </div>
    <div class="room-layers">
      <img src="./../../assets/room-preview/furniture.png" alt="" style="z-index:6;">
      <img src="./../../assets/room-preview/lighting.png" alt="" style="mix-blend-mode: multiply;z-index:5;">
      <img src="./../../assets/room-preview/noise.png" alt="" style="mix-blend-mode: multiply;z-index:4;">
      <img src="./../../assets/room-preview/shadows.png" alt="" style="mix-blend-mode: multiply;z-index:3;">
      <div id="pattern-preview" style="background-color: white;opacity:.85;filter:blur(.5px);z-index:2;mix-blend-mode: multiply;"></div>
      <img src="./../../assets/room-preview/wall.png" alt="" style="z-index: 1;">
    </div>

    <!--
<div class="pattern" style="background-image: url('Space_Planets.png')"></div>
<div class="pattern" style="background-image: url('Flower_of_Life_Outline.png')"></div>
<div class="pattern" style="background-image: url('rightgraphic.PNG')"></div>
<div class="pattern" style="background-image: url('eye_balls.png')"></div>
<div class="pattern" style="background-image: url('Banana_Leaf.png')"></div>
<div class="pattern" style="background-image: url('flatdots.png')"></div>
<div class="pattern" style="background-image: url('Brick_Wall_22.png')"></div>
<div class="pattern" style="background-color: black"></div>
<div class="pattern" style="background-color: #898989"></div>
<div class="pattern" style="background-color: white"></div>
-->
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  data: function() {
    return {
      aspectRatio: 1.408451,
      selectedSize: 7.8
    };
  },
  methods: {
    resizeBox() {
      document.getElementById('room-preview').style.height =
        document.getElementById('room-preview').offsetWidth / this.aspectRatio +
        'px';
      this.resizePattern();
    },
    resizePattern() {
      console.log('resizePattern');
      document.getElementById('pattern-preview').style.backgroundSize =
        window.innerWidth / this.selectedSize + 'px';
    }
  },
  watch: {
    selectedSize() {
      this.resizePattern();
    }
  },
  mounted() {
    window.addEventListener('resize', this.resizeBox);
    this.resizeBox();
    // TODO: Get aspect ratio from actual images themselves
    // this.aspectRatio =
    //   document.getElementsByClassName('room-layers')[0].firstChild.offsetWidth /
    //   document.getElementsByClassName('room-layers')[0].firstChild.offsetHeight;
  },
  beforeDestroy: function() {
    window.removeEventListener('resize', this.resizeBox);
  }
  //     function readURL(input) {
  //   if (input.files && input.files[0]) {
  //     var reader = new FileReader();

  //     reader.onload = function (e) {
  //       console.log(e.srcElement.result);
  //       $('#preview').css('background-image', 'url(' + e.srcElement.result + ')');

  //       $(input).next('.joint-img').attr('src', e.target.result);
  //     }

  //     reader.readAsDataURL(input.files[0]);
  //   }
  // }

  // $(".joint").change(function () {
  //   readURL(this);
  // });
};
</script>



