<template>
  <div class="marketing-panel">
    <div class="content-block">
      <div class="text-wrap">
        <h2>{{marketingContent.data.title[0].text}}</h2>
        <serializer :content='marketingContent.data.body_copy'></serializer>
      </div>
      <div class="button">CTA Here</div>
      <img :src="`${marketingContent.data.image.url}`" :alt="`${marketingContent.data.image.alt}`">
    </div>
  </div>

</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Serializer from './../serializer/Serializer';

export default {
  props: ['page'],
  components: {
    Serializer
  },
  computed: {
    ...mapGetters({
      marketingContent: 'getMarketingPanelFromStore'
    })
  },
  methods: {
    ...mapActions({
      getMarketingPanel: 'getMarketingPanel'
    })
  },
  mounted() {
    this.getMarketingPanel(this.page);
  }
};
</script>



