<template>
  <div class="blog-post page">
    <div class="container">
      <div class="content-block">
        <router-link to="/blog" class="button">back</router-link>
        <h1>{{post.data.title[0].text}}</h1>
        <div class="subheader">{{post.data.subheader[0].text}}</div>
        <span class="subtext">By {{post.data.author[0].text}} on {{post.data.article_date}}</span>
        <!-- <p v-for="paragraph in post.data.blog_content">
          <serializer :content='paragraph'></serializer>
        </p> -->
        <serializer :content='post.data.blog_content'></serializer>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Serializer from './../serializer/Serializer';

export default {
  components: { Serializer },
  name: 'HelloWorld',
  computed: {
    ...mapGetters({
      post: 'getBlogPostFromStore'
    })
  },
  methods: {
    ...mapActions({
      getBlogPostHere: 'getBlogPost'
    })
  },
  mounted: function() {
    this.getBlogPostHere(this.$route.params.post);
    window.scrollTo(0, 0);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



