<template>
  <div class="landing-page-wrap" v-if="data">
    <div class="banner"><img :src="`${data[0].data.header_image.url}`" alt="">
      <h1 class="header">{{data[0].data.landing_page_header[0].text}}</h1>
    </div>
    <div class="container">
      <serializer :content='data[0].data.landing_page_content'></serializer>
    </div>
  </div>
</template>

<script>
import Serializer from './../serializer/Serializer';

export default {
  props: ['data'],
  components: {
    Serializer
  }
};
</script>



