<template>
  <div class="pdp page">
    <div class="container">
      <div class="content-block">
        <calculator-modal></calculator-modal>

        <div class="pdp-wrap">
          <div class="pdp-inner">
            <div class="image-wrap">
              <room-view :pattern="selectedProduct"></room-view>
              <!-- <room-picker></room-picker> -->
            </div>
            <div class="top-right ribbon-wrap">
              <div class="ribbon">Designer Series</div>
            </div>
            <div class="product-detail-wrap">
              <pattern-info :info="patternInfo"></pattern-info>
              <material-picker :materials="materials"></material-picker>
              <size-picker :sizes="sizes"></size-picker>
              <color-picker :colors="pdpColors"></color-picker>
              <div class="show-calculator button" @click="showModal">Calculator</div>
              <quantity-selector></quantity-selector>
              <div class="price">${{selectedProduct.ListPrice}}</div>
              <add-to-cart :product="selectedProduct"></add-to-cart>

              <social-sharing url="https://vuejs.org/" title="The Progressive JavaScript Framework" description="Intuitive, Fast and Composable MVVM for building interactive interfaces." quote="Vue is a progressive framework for building user interfaces." hashtags="vuejs,javascript,framework" twitter-user="vuejs" inline-template>
                <div>

                  <network network="facebook">
                    <span class="social-media-icon icon-facebook"></span>
                  </network>
                  <network network="pinterest">
                    <span class="social-media-icon icon-pinterest"></span>
                  </network>
                  <!--           <network network="reddit">
                    <i class="fa fa-reddit"></i> Reddit
                  </network>-->
                  <network network="twitter">
                    <span class="social-media-icon icon-twitter"></span>
                  </network>

                </div>
              </social-sharing>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import VueGoodshare from 'vue-goodshare';
import SocialSharing from 'vue-social-sharing';
import AddToCart from '@/components/cart/AddToCart';
import MaterialPicker from '@/components/pdp/MaterialPicker';
import SizePicker from '@/components/pdp/SizePicker';
import ColorPicker from '@/components/pdp/ColorPicker';
import RoomPicker from '@/components/pdp/RoomPicker';
import RoomView from '@/components/pdp/RoomView';
import QuantitySelector from '@/components/pdp/QuantitySelector';
import CalculatorModal from '@/components/modals/CalculatorModal';
import PatternInfo from '@/components/pdp/PatternInfo';

export default {
  name: 'PDP',
  components: {
    VueGoodshare,
    SocialSharing,
    AddToCart,
    MaterialPicker,
    ColorPicker,
    RoomView,
    RoomPicker,
    QuantitySelector,
    CalculatorModal,
    SizePicker,
    PatternInfo
  },
  computed: {
    ...mapGetters({
      //TODO : make Pattern Info Grabber
      patternInfo: 'getPatternInfoFromStore',
      selectedProduct: 'getSelectedProductFromStore',
      materialSelected: 'getMaterialSelectedFromStore',
      quantity: 'getQuantitySelectorFromStore',
      pdpColors: 'getPdpColorsFromStore',
      materials: 'getPdpMaterialsFromStore',
      sizes: 'getPdpSizesFromStore'
    })
  },

  methods: {
    showModal() {
      this.$modal.show('calculator');
    },
    hideModal() {
      this.$modal.hide('calculator');
    },
    ...mapActions({
      getPatternInfo: 'getPatternInfo',
      findProduct: 'findPdpProduct'
    })
  },
  created() {
    this.getPatternInfo(this.$route.params);
    window.scrollTo(0, 0);
  }
};
</script>

<style scoped>

</style>



