<template>
  <div class="color-picker">
    <!-- TODO: Fix flickering when selecting a color for the first time -->
    <div class="color-option plain button" v-for="color in colors" :class="{ selected : colorSelected == color.ColorWayName }" @click="selectColor(color.ColorWayName)" :style="{'background-color': color.ColorWayHex}"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  /*    components : {
      WallpaperPattern
    },*/
  props: ['colors'],
  computed: {
    ...mapGetters({
      colorSelected: 'getColorSelectedFromStore'
    })
  },
  methods: {
    ...mapActions({
      selectColor: 'selectColor',
      findPdpProduct: 'findPdpProduct',
      colorExists: 'colorExists'
    })
  },
  updated() {
    this.findPdpProduct();
  }
};
</script>



