<template>
  <div class="filter-search page">
    <landing-content :data="landingContent"></landing-content>
    <div class="container">
      <div class="filter-wrap">
        <ais-index appId="S7MN0CIBBE" apiKey="ecf8c6a506e3515c1400eb7086879aa2">
          <div class="filters">
            <ais-search-box>
              <div class="input-group">
                <ais-input placeholder="Search" class="search-input text-input" />
                <button type="submit">
                  <span class="submit-search-button">Search</span>
                </button>
              </div>
              <!-- /input-group -->
            </ais-search-box>

            <div>
              <!--  {name: 'master_sku', label: 'Master Sku'},-->
              <ais-sort-by-selector :indices="[
                    {name: 'dev_NewEvolutionProducts', label: 'Popular'},

                    {name: 'price_desc', label: 'Price : High to Low'},
                    {name: 'price_asc', label: 'Price : Low to High'}]" />
              <!--   <ais-sort-by-selector :indices="[
                      {name: 'dev_EvolutionProducts', label: 'Relevance'},
                               {name: 'price_desc', label: 'Lowest price'}                ]"
                           >
                             <template slot-scope="{ indexName, label }">
                               <option :value="indexName">Sort by: {{ label }}</option>
                             </template>
                           </ais-sort-by-selector>-->
              <ais-stats/>
            </div>
            <ais-tree-menu :attributes="['DisplayName']">
              <h3 slot="header">Browse by</h3>
            </ais-tree-menu>

            <!--            <ais-price-range attribute-name="price">
                                    <h3 slot="header">Price</h3>
                                </ais-price-range>

            -->
            <ais-refinement-list attribute-name="Colors">
              <h3 slot="header">Color</h3>
            </ais-refinement-list>

            <ais-refinement-list attribute-name="">
              <h3 slot="header">Design Type</h3>
            </ais-refinement-list>

            <!--    <ais-rating attribute-name="rating">
                            <h3 slot="header">Rating</h3>
                        </ais-rating>-->

          </div>
          <transition name="fade">
            <div class="overlay" v-if="mobileFiltersShown" @click="hideMobileFilters"></div>
          </transition>
          <transition name="slide-right">
            <div class="filters-mobile" v-show="mobileFiltersShown">
              <div class="close" @click="hideMobileFilters">X (use icon from menu)</div>
              <ais-refinement-list attribute-name="design.color">
                <h3 slot="header">Color</h3>
              </ais-refinement-list>

              <ais-refinement-list attribute-name="design.type">
                <h3 slot="header">Design Type</h3>
              </ais-refinement-list>
            </div>
          </transition>
          <!-- End Filters -->
          <div class="results">
            <div class="button-wrap under-s">
              <div class="plain button" id="mobile-filters-button" @click="showMobileFilters">Filters</div>
            </div>
            <ais-results>
              <template slot-scope="{ result }">
                <wallpaper-pattern :result="result"></wallpaper-pattern>
              </template>
            </ais-results>

            <ais-no-results/>

            <div class="pagination-wrap">
              <ais-pagination :classNames="{
                    'ais-pagination': 'pagination',
                    'ais-pagination__item--active': 'active',
                    'ais-pagination__item--disabled': 'disabled'
                    }" v-on:page-change="onPageChange" />
              <ais-results-per-page-selector :options="[12, 24, 48]" />
              <span class="results-per-page-text">Results Per Page</span>
            </div>

          </div>

        </ais-index>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import WallpaperPattern from './wallpaper/WallpaperPattern';
import LandingContent from './marketing/LandingContent.vue';

export default {
  data() {
    return {
      results: [],
      onPageChange: function() {
        window.scrollTo(0, 0);
      }
    };
  },
  components: {
    WallpaperPattern,
    LandingContent
  },
  computed: {
    ...mapGetters({
      getSample: 'getSample',
      mobileFiltersShown: 'getShowMobileFilters',
      landingContent: 'getLandingContentFromStore'
    })
  },
  methods: {
    ...mapActions({
      showMobileFilters: 'showMobileFilters',
      hideMobileFilters: 'hideMobileFilters',
      getLandingContent: 'getLandingContent'
    })
  },
  created() {
    window.scrollTo(0, 0);
  },
  updated() {
    /*          var paginationLinks = document.getElementsByClassName('ais-pagination__link');
       for(var z = 0; z < paginationLinks.length; z++) {
       var elem = paginationLinks[z];
       elem.onclick = function() {
       alert("hello");
       window.scrollTo( 0, 0 )
       return false;
       };
       }*/
  },
  created() {
    this.getLandingContent(this.$route.name);
  }
};
</script>



