<template>
  <header>
    <div class="header-cta" id="header-cta-banner" @click="closeMenu">
      <!--TODO : MAKE THIS PRISMIC-->
      Sample Sale - 75% of all items!
    </div>
    <div id="nav-bar">
      <div id="nav-bar-left" class="nav-bar-group">
        <div class="hamburger" v-on:click="toggleMenu" id="hamburger-icon">
          <div class="bar" :class="{animate : showMenu}"></div>
        </div>
        <div id="nav-bar-search">search</div>
      </div>

      <router-link to="/">
        <div id="nav-bar-branding" @click="closeMenu">
          <span class="altero brand-line desktop-only">evolution</span>

          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 429.6 429.6">
            <g data-name="Logo Layer">
              <path class="evolution-svg-logo" d="M0 0v429.6h429.6V0zm405.1 402.7H24.4l380.7-215.9z" data-name="Layer 1" />
            </g>
          </svg>
          <div id="wordmark-container" class=" desktop-only">

          </div>
          <span class="altero brand-line desktop-only">wallpaper</span>

        </div>
      </router-link>

      <div id="nav-bar-right" class="nav-bar-group" @click="closeMenu">
        <span v-if="loggedIn" class="desktop-only">
          <router-link to="/hello">{{userEmail}}</router-link>
        </span>
        <span v-if="loggedIn" class="mobile-only"> profileicon</span>
        <router-link to="cloudinary">RoomPreview</router-link>
        <router-link to="login" v-if="!loggedIn">login</router-link>&nbsp;
        <span v-on:click="logout" v-if="loggedIn">Logout</span>

        <router-link to="/favorites" id="favorites-header-icon">
          <span class="favorites-count">
            <!--{{ getFavoritesCount
          }}-->
          </span>
          <span class=" header-heart icon-heart"></span>
        </router-link>

        <router-link to="/cart" id=" cart-header-icon ">
          <span class="cart-count">
            <!-- {{ getCartCount }}-->
          </span>
          <span class="icon-cart header-cart"></span>
        </router-link>
      </div>

    </div>
    <transition name="fade">
      <div class="fullsize-menu" v-show="showMenu">
        <ul>
          <li>
            <span @click="closeMenu">
              <router-link to="/"> home </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/artists"> artists </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/blog"> blog </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/education"> education </router-link>
            </span>
          </li>
          <li>
            <span @click="closeMenu">
              <router-link to="/wallpaper"> wallpaper </router-link>
            </span>
          </li>
        </ul>
      </div>
    </transition>
  </header>
</template>

<script>
import firebase from 'firebase';

export default {
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      userEmail: '',
      userId: '',
      loggedIn: null,
      showMenu: false
    };
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace('login');
        });
    },
    checkUser: function() {
      var user = firebase.auth().currentUser;

      if (user) {
        console.log(user);
        this.userEmail = user.email;
        this.loggedIn = true;
        // User is signed in.
      } else {
        console.log(user);
        this.userEmail = '';
        this.loggedIn = false;

        // No user is signed in.
      }
      /*    firebase.auth().onAuthStateChanged(function(user) {
         if (user) {
         this.nameFromServer = user.fullname;
         console.log(user)
         } else {
         console.log(user)

         // No user is signed in.
         }
         });*/
      /*
         let userId = firebase.auth().currentUser.uid;

         return firebase.database().ref('/users/' + userId).once('value').then(snapshot => {
         this.nameFromServer = (snapshot.val() && snapshot.val().fullName) || 'Anonymous';
         });*/
    },
    closeMenu() {
      this.showMenu = false;
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    }
  },
  mounted() {
    this.checkUser();
  },
  watch: {
    $route(to, from) {
      this.checkUser();
    }
  }
};
</script>



